import type { UBQResult } from '../types/engine';

export function unpackResult<T>(result: UBQResult<T>): T {
  if (result.isValid()) {
    const value = result.value();
    result.delete();
    return value;
  } else {
    const error = result.error();
    const errorMessage = IMGLY_INTERNAL_DEPLOY
      ? error.message()
      : error.publicMessage();
    error.delete();
    result.delete();
    throw new Error(errorMessage);
  }
}
/**
 * @example
 * ```ts
 * function loadFromString(s: string):Promise<DesignBlockId> {
 *   return unpackAsync((cb) => this.ubq.loadSceneFromString(s, cb))
 * }
 * ```
 */
export function unpackAsync<T>(
  invoker: (callback: (result: UBQResult<T>) => void) => void
): Promise<T> {
  return new Promise((resolve, reject) => {
    invoker((ubqResult) => {
      try {
        const result = unpackResult(ubqResult);
        resolve(result);
      } catch (e) {
        reject(e);
      }
    });
  });
}
