/**
 * This contraption is necessary because in Node 16, Blob is not available globally,
 * but needs to be imported from 'buffer. In the long term, this should not be necessary,
 * Node 18 and Browsers already support the global Blob constructor.
 */
export default typeof Blob !== 'undefined'
  ? Blob
  : ((() => {
      try {
        // @ts-ignore
        return require('buffer').Blob;
      } catch (e) {
        return function Blob() {
          throw new Error(
            "Could not find Blob constructor and failed to require('buffer').Blob"
          );
        };
      }
    })() as typeof Blob);
