import { getter } from '../reactor/decorate';
import type { Source } from '../streams';
import type { DesignBlockTypeLonghand } from '../types/blockTypes';
import {
  NotificationType,
  SceneLayoutNumeric,
  type ClampArgs,
  type ClampCameraCommandResult,
  type DesignBlockId,
  type KeyboardKeyEvent,
  type MouseButtonEvent,
  type MouseMoveEvent,
  type SceneLayout,
  type TouchPanEvent,
  type TouchPinchEvent,
  type TouchRotateEvent,
  type UBQResult,
  type Vec2
} from '../types/engine';
import mapToEngineKey from '../utils/mapToEngineKey';
import type { UbiqueEngine } from './ubique';
import type { UBQ } from './ubq_binding';
import { unpackResult } from './ubq_result';

/**
 * Wrapper around Ubique that removes all the C++ weirdness
 * @internal
 */
export default class LegacyAPI {
  private ubique: UbiqueEngine;

  constructor(ubq: UBQ) {
    this.ubique = ubq.getInternalAPI();
  }

  dispose() {
    this.ubique.delete();
  }

  setErrorCallback(
    callback: (buffer: string) => void,
    abortOnError: boolean
  ): void {
    this.ubique.setErrorCallback(callback, abortOnError);
  }

  notificationStream: Source<{
    type: 'Information' | 'Warning' | 'Error';
    i18n: string;
  }> = (handler) => {
    const disposable = this.ubique.addEventCallback('NotificationEvent', (e) =>
      handler({
        type: NotificationType[e.type] as 'Information' | 'Warning' | 'Error',
        i18n: e.i18n
      })
    );
    return () => {
      disposable.dispose();
      disposable.delete();
    };
  };

  designElementLifecycleStream =
    (
      event:
        | 'DesignBlockAddedEvent'
        | 'DesignBlockUpdatedEvent'
        | 'DesignBlockRemovedEvent'
    ): Source<{ entity: DesignBlockId; typeName: string }> =>
    (handler) => {
      const disposable = this.ubique.addEventCallback(event, handler);
      return () => {
        disposable.dispose();
        disposable.delete();
      };
    };

  historyStream: Source<void> = (handler) => {
    const disposable = this.ubique.addEventCallback(
      'HistoryUpdatedEvent',
      handler
    );
    return () => {
      disposable.dispose();
      disposable.delete();
    };
  };

  @getter
  getSelectedText(): string {
    return unpackResult(this.ubique.getSelectedText());
  }

  @getter
  hasComponent(id: DesignBlockId, component: string): boolean {
    return unpackResult(this.ubique.hasComponent(id, component));
  }

  @getter
  getValue<T>(id: DesignBlockId, component: string, keyPath: string): T {
    return unpackResult<T>(this.ubique.getValue<T>(id, component, keyPath));
  }

  execute(
    command: 'cesdk/clampCameraAndRender',
    args: ClampArgs
  ): Promise<ClampCameraCommandResult>;
  execute(command: 'cesdk/getCapabilitiesInfo'): Promise<string>;
  execute(command: 'cesdk/getVersionInfo'): Promise<string>;
  /**
   * @deprecated Replace with EditorAPI commands
   *
   * Used in UndoManager
   */
  execute(
    command: 'ubq/editor/history/createSnapshot',
    args: [name: string, resyncCamera: boolean]
  ): Promise<void>;
  /**
   * @deprecated Use BlockAPI instead
   *
   * Used in ImagePositionTransform
   */
  execute(
    command: 'ubq/editor/setAbsoluteCropTranslation',
    args: { value: Vec2 }
  ): Promise<void>;
  /**
   * @deprecated Use BlockAPI instead
   *
   * Used in CropAreaTransform
   */
  execute(
    command: 'ubq/editor/setCropAreaSize',
    args: { size: Vec2 }
  ): Promise<void>;
  /**
   * Send input to the engine. Not exposed externally, can remain a command
   */
  execute(
    command: 'ubq/inputs/keyboardkey',
    arg: KeyboardKeyEvent
  ): Promise<void>;
  /**
   * Send input to the engine. Not exposed externally, can remain a command
   */
  execute(
    command: 'ubq/inputs/mousebutton',
    arg: MouseButtonEvent
  ): Promise<void>;
  /**
   * Send input to the engine. Not exposed externally, can remain a command
   */
  execute(command: 'ubq/inputs/mousemove', arg: MouseMoveEvent): Promise<void>;
  /**
   * Send input to the engine. Not exposed externally, can remain a command
   */
  execute(
    command: 'ubq/inputs/touchrotate',
    arg: TouchRotateEvent
  ): Promise<void>;
  /**
   * Send input to the engine. Not exposed externally, can remain a command
   */
  execute(command: 'ubq/inputs/touchpan', arg: TouchPanEvent): Promise<void>;
  /**
   * Send input to the engine. Not exposed externally, can remain a command
   */
  execute(
    command: 'ubq/inputs/touchpinch',
    arg: TouchPinchEvent
  ): Promise<void>;
  /**
   * @deprecated Used by copy/paste, replace with method on UBQ (sync or async).
   * Copy/paste is otherwise not available to customers
   * BlockAPI.loadFromString
   *
   * Used in Facade.ubqExecute
   */
  execute(
    command: 'ubq/loadElements',
    args: { elementsContent: string; setHidden: boolean }
  ): Promise<
    {
      id: DesignBlockId;
      sourceId: DesignBlockId;
      sourceParent: DesignBlockId;
    }[]
  >;
  execute(command: 'ubq/render'): Promise<void>;
  /**
   * @deprecated Replace with synchronous method on UBQ or with a property setter on the scene
   */
  execute(
    command: 'ubq/scene/changeLayout',
    args: [
      sceneId: DesignBlockId,
      layout: SceneLayoutNumeric,
      spacing: number,
      spacingInScreenSpace: boolean
    ]
  ): Promise<void>;
  /**
   * @deprecated Replace usage with BlockApi.findAll, only used in useAllDesignElements hook
   * Currently this is the only way to get the selectable design elements
   */
  execute(command: 'ubq/scene/getAll'): Promise<
    Array<{
      id: DesignBlockId;
      typeId: DesignBlockTypeLonghand;
      isSelectable?: boolean;
    }>
  >;
  /**
   * @deprecated
   * TextInputSystem is still completely missing from the new API
   */
  execute(command: 'ubq/text/compositionEnd'): Promise<void>;
  /**
   * @deprecated
   * TextInputSystem is still completely missing from the new API
   */
  execute(command: 'ubq/text/compositionStart'): Promise<void>;
  /**
   * @deprecated
   * TextInputSystem is still completely missing from the new API
   */
  execute(
    command: 'ubq/text/compositionUpdate',
    arg: { value: string; cursorPosition: number }
  ): Promise<void>;
  /**
   * @deprecated
   * TextInputSystem is still completely missing from the new API
   * Already exists in sync api
   */
  execute(command: 'ubq/text/selectAllText'): Promise<void>;
  execute<T>(command: string, args?: unknown): Promise<T> {
    let executeResult: UBQResult<void>;
    const resultPromise = new Promise<T>((resolve, reject) => {
      executeResult = this.ubique.ubqExecute<T>(command, args, (rawResult) => {
        try {
          const result = unpackResult(rawResult);
          resolve(result);
        } catch (e) {
          reject(e);
        }
      });
    });
    // This will throw when there was an error while queueing the command
    unpackResult(executeResult!);
    return resultPromise;
  }

  /**
   * Convencience helper to send keyboard events into the engine
   */
  sendKey(event: {
    key: string;
    shiftKey: boolean;
    metaKey: boolean;
    ctrlKey: boolean;
    altKey: boolean;
  }) {
    return this.execute('ubq/inputs/keyboardkey', mapToEngineKey(event));
  }

  /** Convenience helper to avoid having to work with numeric scene layouts in the CESDK */
  changeSceneLayout(
    scene: DesignBlockId,
    layout: SceneLayout,
    spacing: number,
    spacingInScreenSpace: boolean
  ) {
    return this.execute('ubq/scene/changeLayout', [
      scene,
      SceneLayoutNumeric[layout],
      spacing,
      spacingInScreenSpace
    ]);
  }
}
