import BlockAPI from '../BlockAPI';
import EditorAPI from '../EditorAPI';
import SceneAPI from '../SceneAPI';
import type { Configuration } from '../configuration';
import init from '../init';
import type { ChannelSync } from '../streams/Channel';
import MimeType from '../types/MimeType';
import type { DesignBlockId } from '../types/engine';
import type { Scope } from '../types/scope';
import { importSettings } from './transferSettings';
import type { WorkerOptions } from './workerTypes';

/**
 * If the number of frames in the encoder queue is `>= MAX_QUEUE_SIZE`,
 * skip rendering new frames;
 */
const MAX_QUEUE_SIZE = 10;

/** @internal */
export type ExportOptions = {
  block?: DesignBlockId;
  h264Profile?: number;
  h264Level?: number;
  videoBitrate?: number;
  audioBitrate?: number;
  timeOffset?: number;
  duration?: number;
  width?: number;
  height?: number;
  framerate?: number;
};

declare global {
  /* eslint-disable no-var, vars-on-top */
  /**
   * Used to communicate the status to C++ in CodecService-emscripten
   * from the exportVideo task
   */
  var ubq_browserTabHidden: boolean;
  /* eslint-enable no-var, vars-on-top */
}

globalThis.ubq_browserTabHidden = true;

/**
 * Instantiates an engine with an offscreencavas and exports a video.
 * @internal
 */
export default async function exportVideo(
  config: Configuration & { abortSignal?: AbortSignal },
  settings: Map<string, any>,
  sceneString: string,
  options: ExportOptions,
  workerOptions: WorkerOptions,
  runOptions: {
    onProgress: (
      renderedFrames: number,
      encodedFrames: number,
      totalFrames: number
    ) => void;
    visibility?: ChannelSync<boolean>;
    inactivityTimeout: number;
  }
): Promise<Uint8Array> {
  /** Number of frames in the encode queue (rendered, but not encoded) */
  let queueSize = 0;

  let lastActivity: number | null = null;

  let rejectExport: (reason?: any) => void;

  let timeout: ReturnType<typeof setTimeout>;

  const visibilityChannel = runOptions.visibility ?? defaultVisibility;

  const inactivityTimeout = runOptions.inactivityTimeout;

  const unsubscribeVisibility = visibilityChannel.subscribe(() => {
    // Reset the inactivity timer to avoid aborting the export right when the tab becomes visible again.
    if (lastActivity !== null) {
      lastActivity = Date.now();
    }
    globalThis.ubq_browserTabHidden = !visibilityChannel.value();
  });

  let disposeEngine: (() => void) | null = null;

  function dispose() {
    clearTimeout(timeout);
    unsubscribeVisibility();
    disposeEngine?.();
  }

  try {
    const canvas = new OffscreenCanvas(64, 64);
    const ubq = await init(canvas, { ...config, audioOutput: 'none' });
    const sceneApi = new SceneAPI(ubq);
    const blockApi = new BlockAPI(ubq);
    const editorApi = new EditorAPI(ubq);

    disposeEngine = () => {
      ubq.delete();
    };

    importSettings(editorApi, settings);

    if (workerOptions?.trackingMetadata) {
      editorApi.setTrackingMetadata(workerOptions.trackingMetadata);
    }

    if (workerOptions?.uriResolver) {
      // - The (0, eval) is required to avoid "direct eval"
      //   https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/eval#never_use_direct_eval!
      // - The "use strict" is required to avoid accidental pollution of the
      //   global scope
      // - The parentheses are necessary to ensure that a function definition is
      //   evaluated as an expression and not as a statement
      //
      // eslint-disable-next-line no-eval
      const uriResolver = (0, eval)(
        `'use strict';(${workerOptions.uriResolver})`
      );

      function resolverWithError(
        uri: string,
        defaultURIResolver: (uri: string) => string
      ) {
        try {
          return uriResolver(uri, defaultURIResolver);
        } catch (e) {
          // eslint-disable-next-line no-console
          console.warn(
            `Error during execution of URI resolver: ${e}.\nMake sure the url resolver function does not reference any external variables. Falling back to default URI resolver.`
          );
          return defaultURIResolver(uri);
        }
      }
      editorApi.setURIResolver(resolverWithError);
    }

    if (workerOptions?.scopes) {
      for (const [scope, value] of Object.entries(workerOptions.scopes)) {
        editorApi.setGlobalScope(scope as Scope, value);
      }
    }

    if (workerOptions?.buffers) {
      editorApi.restoreBuffers(workerOptions.buffers);
    }

    const sceneId = await sceneApi.loadFromString(sceneString);
    const exportVideoDuration = options.block
      ? blockApi.getDuration(options.block)
      : blockApi.getTotalSceneDuration(sceneApi.get()!);

    function update() {
      // Don't abort the export if the tab is hidden.
      if (visibilityChannel.value() === true && lastActivity !== null) {
        if (Date.now() - lastActivity > inactivityTimeout) {
          rejectExport(new Error('Video export timed out due to inactivity.'));
          return;
        }
      }

      if (queueSize < MAX_QUEUE_SIZE) {
        ubq.update();
      }
      timeout = setTimeout(update, 1);
    }

    update();

    const data = await new Promise<Uint8Array>((resolve, reject) => {
      rejectExport = reject;

      config.abortSignal?.addEventListener('abort', () => {
        reject(new Error('AbortSignal received'));
      });

      ubq.exportVideoToBuffer(
        options.block ?? sceneId,
        options.timeOffset ?? 0,
        options.duration ?? exportVideoDuration,
        MimeType.Mp4,
        (renderedFrames, encodedFrames, totalFrames) => {
          queueSize = renderedFrames - encodedFrames;
          lastActivity = Date.now();
          runOptions?.onProgress?.(renderedFrames, encodedFrames, totalFrames);
        },
        (exportResult) => {
          if ('error' in exportResult) {
            reject(exportResult.error);
          } else {
            resolve(exportResult);
          }
        },
        {
          h264Profile: options.h264Profile ?? 77, // Main
          h264Level: options.h264Level ?? 52, // 5.2
          framerate: options.framerate ?? 30,
          videoBitrate: options.videoBitrate ?? 0,
          audioBitrate: options.audioBitrate ?? 0,
          useTargetSize:
            options.width !== undefined && options.height !== undefined,
          targetWidth: options.width ?? 0,
          targetHeight: options.height ?? 0
        }
      );
    });

    const videoData = new Uint8Array(data.byteLength);
    videoData.set(data);
    dispose();
    return videoData;
  } catch (e) {
    dispose();
    throw e;
  }
}

const defaultVisibility: ChannelSync<boolean> = {
  value() {
    if (typeof document === 'undefined') {
      return true;
    } else {
      return document.visibilityState === 'visible';
    }
  },
  subscribe(handler) {
    if (typeof document === 'undefined') {
      return () => {};
    } else {
      const handleChange = () => {
        handler(document.visibilityState === 'visible');
      };
      document.addEventListener('visibilitychange', handleChange);
      return () => {
        document.removeEventListener('visibilitychange', handleChange);
      };
    }
  }
};
