import EditorAPI, {
  type SettingsBool,
  type SettingsColor,
  type SettingsEnum,
  type SettingsFloat,
  type SettingsString
} from '../EditorAPI';

/**
 * Export all current settings.
 * @param editor - The EditorAPI wrapping a UBQ instance.
 * @returns The settings as a key value map.
 * @internal
 */
export function exportSettings(editor: EditorAPI): Map<string, any> {
  const result = new Map<string, any>();
  const settings = editor.findAllSettings();
  for (const setting of settings) {
    switch (editor.getSettingType(setting)) {
      case 'Bool':
        result.set(setting, editor.getSettingBool(setting as SettingsBool));
        break;
      case 'Int':
        result.set(setting, editor.getSettingInt(setting));
        break;
      case 'Float':
        result.set(setting, editor.getSettingFloat(setting as SettingsFloat));
        break;
      case 'String':
        result.set(setting, editor.getSettingString(setting as SettingsString));
        break;
      case 'Color':
        result.set(setting, editor.getSettingColor(setting as SettingsColor));
        break;
      case 'Enum':
        result.set(
          setting,
          editor.getSettingEnum(setting as keyof SettingsEnum)
        );
        break;
      default:
        break;
    }
  }
  return result;
}

/**
 * Import settings from a key value map.
 * @param editor - The EditorAPI wrapping a UBQ instance.
 * @param settings - The settings to import.
 * @internal
 */
export function importSettings(
  editor: EditorAPI,
  settings: Map<string, any>
): void {
  for (const [key, value] of settings) {
    switch (editor.getSettingType(key)) {
      case 'Bool':
        editor.setSettingBool(key as SettingsBool, value);
        break;
      case 'Int':
        editor.setSettingInt(key, value);
        break;
      case 'Float':
        editor.setSettingFloat(key as SettingsFloat, value);
        break;
      case 'String':
        editor.setSettingString(key as SettingsString, value);
        break;
      case 'Color':
        editor.setSettingColor(key as SettingsColor, value);
        break;
      case 'Enum':
        {
          const _key = key as keyof SettingsEnum;
          const _value = value as SettingsEnum[typeof _key];
          editor.setSettingEnum(_key, _value);
        }
        break;
      default:
        break;
    }
  }
}
