  Png = 'image/png',
  Jpeg = 'image/jpeg',
  WebP = 'image/webp',
  Tga = 'image/x-tga',
  Wav = 'audio/wav',
  Mp4 = 'video/mp4',
  QuickTime = 'video/quicktime',
  Binary = 'application/octet-stream',
  Pdf = 'application/pdf',
  Zip = 'application/zip'
