import { KeyboardKey, type KeyboardKeyEvent } from '../types/engine';

export default function mapToEngineKey(event: {
  key: string;
  shiftKey: boolean;
  metaKey: boolean;
  ctrlKey: boolean;
  altKey: boolean;
}): KeyboardKeyEvent {
  const result: KeyboardKeyEvent = {
    key: KeyboardKey.Unknown,
    characters: event.key,
    shiftIsHeld: event.shiftKey,
    commandIsHeld: event.metaKey || event.ctrlKey,
    optionIsHeld: event.altKey,
    timestamp: Date.now()
  };
  // FIXME: Make sure we filter out all special keys out here
  switch (event.key.toUpperCase()) {
    case '0':
      result.key = KeyboardKey.Key0;
      break;
    case '1':
      result.key = KeyboardKey.Key1;
      break;
    case '2':
      result.key = KeyboardKey.Key2;
      break;
    case '3':
      result.key = KeyboardKey.Key3;
      break;
    case '4':
      result.key = KeyboardKey.Key4;
      break;
    case '5':
      result.key = KeyboardKey.Key5;
      break;
    case '6':
      result.key = KeyboardKey.Key6;
      break;
    case '7':
      result.key = KeyboardKey.Key7;
      break;
    case '8':
      result.key = KeyboardKey.Key8;
      break;
    case '9':
      result.key = KeyboardKey.Key9;
      break;
    case 'A':
      result.key = KeyboardKey.A;
      break;
    case 'B':
      result.key = KeyboardKey.B;
      break;
    case 'C':
      result.key = KeyboardKey.C;
      break;
    case 'D':
      result.key = KeyboardKey.D;
      break;
    case 'E':
      result.key = KeyboardKey.E;
      break;
    case 'F':
      result.key = KeyboardKey.F;
      break;
    case 'G':
      result.key = KeyboardKey.G;
      break;
    case 'H':
      result.key = KeyboardKey.H;
      break;
    case 'I':
      result.key = KeyboardKey.I;
      break;
    case 'J':
      result.key = KeyboardKey.J;
      break;
    case 'K':
      result.key = KeyboardKey.K;
      break;
    case 'L':
      result.key = KeyboardKey.L;
      break;
    case 'M':
      result.key = KeyboardKey.M;
      break;
    case 'N':
      result.key = KeyboardKey.N;
      break;
    case 'O':
      result.key = KeyboardKey.O;
      break;
    case 'P':
      result.key = KeyboardKey.P;
      break;
    case 'Q':
      result.key = KeyboardKey.Q;
      break;
    case 'R':
      result.key = KeyboardKey.R;
      break;
    case 'S':
      result.key = KeyboardKey.S;
      break;
    case 'T':
      result.key = KeyboardKey.T;
      break;
    case 'U':
      result.key = KeyboardKey.U;
      break;
    case 'V':
      result.key = KeyboardKey.V;
      break;
    case 'W':
      result.key = KeyboardKey.W;
      break;
    case 'X':
      result.key = KeyboardKey.X;
      break;
    case 'Y':
      result.key = KeyboardKey.Y;
      break;
    case 'Z':
      result.key = KeyboardKey.Z;
      break;
    case ' ':
      result.key = KeyboardKey.Space;
      break;
    case 'ESCAPE':
      result.key = KeyboardKey.Escape;
      result.characters = '';
      break;
    case 'BACKSPACE':
      result.key = KeyboardKey.Backspace;
      result.characters = '';
      break;
    case 'DELETE':
      result.key = KeyboardKey.Delete;
      result.characters = '';
      break;
    case 'ENTER':
      result.key = KeyboardKey.Enter;
      result.characters = result.shiftIsHeld ? '\u2028' : '\n';
      break;
    case 'CONTROL':
      result.key = KeyboardKey.Control;
      result.characters = '';
      break;
    case 'ALT':
      result.key = KeyboardKey.Option;
      result.characters = '';
      break;
    case 'SHIFT':
      result.key = KeyboardKey.Shift;
      result.characters = '';
      break;
    case 'ARROWLEFT':
      result.key = KeyboardKey.ArrowLeft;
      result.characters = '';
      break;
    case 'ARROWRIGHT':
      result.key = KeyboardKey.ArrowRight;
      result.characters = '';
      break;
    case 'ARROWUP':
      result.key = KeyboardKey.ArrowUp;
      result.characters = '';
      break;
    case 'ARROWDOWN':
      result.key = KeyboardKey.ArrowDown;
      result.characters = '';
      break;
    case 'DEAD':
      result.key = KeyboardKey.Unknown;
      result.characters = '';
      break;
    default:
      break;
  }
  return result;
}
