import type { Configuration } from './configuration';
import configureEngine from './configure';
import { downloadAndCheckLicense, unlockUbq } from './license';
import { CESDK_DATA, CESDK_JS, CESDK_WASM } from './ubq/module';
import type { UBQ } from './ubq/ubq_binding';
import { unpackResult } from './ubq/ubq_result';
import { isMobileWebKit } from './ui/browser';

declare global {
  interface EmscriptenModule {
    printWithColors?: boolean;
    wasmMemory?: WebAssembly.Memory;
  }
}

const changeAssetPath = (initialPath: string, assetPath?: string) => {
  if (assetPath != null) {
    const fileName = initialPath.split('/').pop();
    return [assetPath, fileName].join('');
  }

  return new URL(initialPath, window.location.origin);
};

function getWasmMemory() {
  try {
    return new WebAssembly.Memory({
      initial: getWasmPageLimitFromMegabytes(isMobileWebKit ? 512 : 32),
      maximum: getWasmPageLimitFromMegabytes(2048)
    });
  } catch (e) {
    return new WebAssembly.Memory({
      initial: getWasmPageLimitFromMegabytes(32),
      maximum: getWasmPageLimitFromMegabytes(2048)
    });
  }
}

function getWasmPageLimitFromMegabytes(limitInMegabytes: number): number {
  return (limitInMegabytes * 1024 * 1024) / 65536;
}

export default async function init(
  canvasElement: HTMLCanvasElement | OffscreenCanvas,
  config: Configuration
) {
  const { core } = config;
  const log = config.logger;
  const wasmPath = changeAssetPath(CESDK_WASM, core.baseURL).toString();
  const dataPath = changeAssetPath(CESDK_DATA, core.baseURL).toString();
  const targetIsElectron = /electron/i.test(navigator.userAgent);
  // We define the `getPreloadedPackage` callback only for use with Electron,
  // to avoid the Electron renderer process attempting to access the Node file system API (fs).

  const license = downloadAndCheckLicense(config);

  const getPreloadedPackage = targetIsElectron
    ? await fetch(dataPath, { credentials: 'same-origin' })
        .then((res) => res.arrayBuffer())
        .then((buffer) => {
          // eslint-disable-next-line @typescript-eslint/no-shadow
          return function getPreloadedPackage(
            name: string,
            size: number
          ): ArrayBuffer {
            if (name !== dataPath) {
              // @ts-ignore @types/emscripten is wrong
              return null;
            }
            if (size === buffer.byteLength) {
              return buffer;
            } else {
              throw new Error(
                `Attempt to get preloaded package of unknown name or size: ${name} ${size}`
              );
            }
          };
        })
    : undefined;

  return new Promise<UBQ>((resolveInit, rejectInit) => {
    const emscriptenConfig: Partial<EmscriptenModule> = {
      getPreloadedPackage,
      locateFile: (path: string, prefix: string) => {
        if (path.endsWith('.wasm')) {
          return wasmPath;
        }
        if (path.endsWith('.data')) {
          return dataPath;
        }

        return prefix + path;
      },
      print: (message: string) => {
        if (message && message !== 'undefined') {
          log(message, 'Info');
        }
      },
      printErr: (message: string) => {
        if (message && message !== 'undefined') {
          log(message, 'Error');
        }
      },
      logReadFiles: false,
      printWithColors: true,
      wasmMemory: getWasmMemory()
    };

    CESDK_JS(emscriptenConfig)
      .then(async (ModuleInstance) => {
        // Note: Video encoding relies on the OffscreenCanvas being available from this dictionary.
        ModuleInstance.specialHTMLTargets['!canvas'] = canvasElement;
        const target = '!canvas';
        // Note: Mobile WebKit has a bug in its WebGL 2.0 implementation that causes
        // the glReadPixels call during the export to lead to a page reload.
        // However, when forcing WebGL 1.0, we end up with random text rendering issues in mobile Safari.
        // This issue is slightly more critical than the broken in-browser image exports, so we keep
        // WebGL 2.0 enabled by default for now.
        ModuleInstance.emscripten_ubq_settings_forceWebGL1 =
          isMobileWebKit || !!config?.forceWebGL1;

        const engine = unpackResult(
          ModuleInstance.createEngine(target, config.audioOutput ?? 'auto')
        );

        unlockUbq(engine, await license);
        configureEngine(engine, config);
        resolveInit(engine);
      })
      .catch(rejectInit);
  });
}
