/* eslint-disable @typescript-eslint/no-loop-func */
import type Reactor from './Reactor';

const PROXY = Symbol('PROXY');

type ProxyMeta = {
  type: 'getter' | 'setter';
};

function isFnProxy(property: any): property is { [PROXY]: ProxyMeta } {
  return typeof property === 'function' && PROXY in property;
}

/* eslint-disable @typescript-eslint/no-unused-vars */

/**
 * Decorator to mark a function as a getter
 * @internal
 */
export function getter(
  target: Function,
  context?: ClassMethodDecoratorContext
): void {
  Object.defineProperty(target, PROXY, {
    value: { type: 'getter' },
    writable: false
  });
}

/**
 * Decorator to mark a function as a setter
 * @internal
 */
export function setter(
  target: Function,
  context?: ClassMethodDecoratorContext
): void {
  Object.defineProperty(target, PROXY, {
    value: { type: 'setter' },
    writable: false
  });
}

/* eslint-enable @typescript-eslint/no-unused-vars */

/**
 * Decorate a single function to be tracked by the reactor.
 * @param fnName - Name of the function to decorate. Used to generate the
 *                 callId, so make sure it's unique with regard to its context.
 * @param func - The function to decorate.
 * @returns The decorated function. Calling this function will track the call
 *          in the Reactor and provide caching in reactions.
 */
export function decorateFunction<A extends any[], R>(
  reactor: Reactor,
  func: (...args: A) => R,
  fnName: string = func.name
): (...args: A) => R {
  readFunctionProxyCounter[fnName] ??= 0;
  const index = readFunctionProxyCounter[fnName]++;

  return function proxiedReadFunction(this: any, ...argArray: A): R {
    const callReceiver = this;
    const callId = `${fnName}${index}-${JSON.stringify(argArray, null, 0)}`;

    function callTargetMethod(): R {
      return Reflect.apply(func, callReceiver, argArray);
    }

    reactor._getterCalled(callId, callTargetMethod);
    return reactor.read(callId, callTargetMethod);
  };
}

/**
 * If multiple ReadFunctions are registered with the same name, we need to
 * make sure that the callId is unique. This counter keeps track of the
 * number of times a ReadFunction with a certain name has been registered.
 */
const readFunctionProxyCounter: { [fnName: string]: number } = {};

/**
 * Wraps all marked `@getter` and `@setter` functions on an object with code
 * that registers their invocations with the Reactor
 *
 * @returns A function that can be called to stop tracking the object.
 *
 * @internal
 */
export default function decorateObject<T extends object>(
  reactor: Reactor,
  object: T
): () => void {
  const objectId = (object as any).constructor?.name ?? crypto.randomUUID();
  const disposers: (() => void)[] = [];
  let disposed = false;

  // In a function that uses `this.#ubq`, or another private property,
  // without an annotation we get the private property access error, because
  // during invocation, `this` is the proxy, but we're not exchanging the
  // callReceiver, like we do in the proxied methods (marked as gettters or setters)
  const properties = Object.getOwnPropertyNames(
    object.constructor.prototype
  ).concat(Object.getOwnPropertyNames(object)) as (keyof T)[];

  for (const fnName of properties) {
    const func = object[fnName];
    if (!isFnProxy(func)) {
      // Enable this to find API calls that are not decorated with @getter or @setter
      //
      // if (IMGLY_NODE_ENV === 'development' && fnName !== 'constructor') {
      //   const fn = object[fnName];
      //   if (typeof fn === 'function') {
      //     // eslint-disable-next-line no-console
      //     console.warn(
      //       'Function',
      //       fnName,
      //       'is not decorated with @getter or @setter in',
      //       objectId
      //     );
      //   }
      // }
      continue;
    }

    if (func[PROXY].type === 'getter') {
      function getterProxy<R>(this: any, ...argArray: any[]): R {
        if (disposed) {
          throw new Error('Disposed @getter proxy called');
        }
        const callReceiver = this;
        const callId = `${objectId}-${fnName.toString()}-${JSON.stringify(
          argArray,
          null,
          0
        )}`;
        function callTargetMethod(): R {
          return Reflect.apply(func as Function, callReceiver, argArray);
        }

        reactor._getterCalled(callId, callTargetMethod);
        return reactor.read(callId, callTargetMethod);
      }
      disposers.push(createRestoreFunction(object, fnName, func));
      object[fnName] = getterProxy as any;
    } else if (func[PROXY].type === 'setter') {
      function setterProxy<R>(this: any, ...argArray: any[]): R {
        if (disposed) {
          throw new Error('Disposed @setter proxy called');
        }
        const callReceiver = this;
        function callTargetMethod(): R {
          return Reflect.apply(func as Function, callReceiver, argArray);
        }

        reactor._setterCalled();
        return callTargetMethod();
      }
      disposers.push(createRestoreFunction(object, fnName, func));
      object[fnName] = setterProxy as any;
    }
  }

  return function disposeProxy() {
    disposed = true;
    for (const disposer of disposers) disposer();
    disposers.length = 0;
  };
}

/**
 * Create a function that restores the original value of a property
 * @param o - The target object
 * @param name - The name of the property
 * @param original - The original value of the property
 * @returns The restore function
 */
function createRestoreFunction(
  o: any,
  name: string | number | symbol,
  original: unknown
): () => void {
  if (Object.hasOwnProperty.call(o, name)) {
    // property defined on the object itself, overwrite
    return () => {
      o[name] = original;
    };
  } else {
    // property defined on a prototype, delete
    return () => {
      delete o[name];
    };
  }
}
