import type { Vector } from '../types/engine';

export default <T>(stdVector: Vector<T>, dispose: boolean = true): Array<T> => {
  const ids = [];
  for (let index = 0; index < stdVector.size(); index += 1) {
    ids.push(stdVector.get(index));
  }
  if (dispose) stdVector.delete();

  return ids;
};
